<template>
  <div id="home">
    <el-container>
      <el-aside>
        <homeboxleft @show="itemClick" :username="username"></homeboxleft>
      </el-aside>
      <el-container>
        <el-header style="padding: 0;">
          <homeboxtop  :username="username"></homeboxtop>
        </el-header>
        <el-main>
          <homeboxmain :index1="i" :peopleNum="peopleNum"></homeboxmain>
          
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import homeboxleft from "./homeChildren/HomeBoxLeft";
import homeboxtop from "./homeChildren/HomeBoxTop";
import homeboxmain from "./homeChildren/HomeBoxMain";

import { req1 } from "@/network/request.js";

export default {
  data() {
    return {
      i: 1,
      username: "",
      peopleNum: 0
    };
  },
  components: {
    homeboxleft,
    homeboxtop,
    homeboxmain,
    
  },
  methods: {
    itemClick(i) {
      this.i = i;
    }
  },
  created() {
    localStorage.getItem('username')
    this.username = localStorage.getItem("username");
    console.log(this.username);

    req1({
      url: '/userNumber'
    }).then(res => {
      this.peopleNum = res
    })
  }
};
</script>

<style>
#home {
  height: 100%;
}
el-aside {
  width: 300px;
}
el-header {
  padding: 0;
}
</style>