<template>
  <div id="detail">
    <toast :chargeMsg="chargeMsg" :show="show" :orderCarList="orderCarList" :i="i" @closeToast="closeToast" />
    <div class="detail_title">管理员快捷入口</div>
    <div class="navigates">
      <div class="charge_img">
        <a href="javascript:;" @click="showToast(0)">
          <img src="@/assets/imgs/charge.png" />
          <br />
          <span>交易记录</span>
        </a>
      </div>
      <div class="charge_img">
        <a href="javascript:;" @click="showToast(1)">
          <img src="@/assets/imgs/park.png" />
          <br />
          <span>车位列表</span>
        </a>
      </div>
      <div class="charge_img">
        <a href="javascript:;" @click="showToast(2)">
          <img src="@/assets/imgs/order.png" />
          <br />
          <span>订单列表</span>
        </a>
      </div>
      <div class="charge_img">
        <a href="javascript:;" @click="showToast(3)">
          <img src="@/assets/imgs/search.png" />
          <br />
          <span>搜索订单</span>
        </a>
      </div>
    </div>
    <!-- 查询订单 -->
    <el-drawer title="我是标题" :visible.sync="drawer" :with-header="false">
      <div class="search_input">
        <SearchInput :placeholder="placeholder" ref="order"></SearchInput>
      </div>
      <div class="mousemove" @mousemove="getOrder()"></div>
      <OrderList v-if="orderList.id" :orderList="orderList"></OrderList>
      <div class="hidden" v-else>请输入订单号查询！</div>
    </el-drawer>
    <!-- 车位列表 -->
    <el-drawer title="我是标题1" :visible.sync="drawer1" :with-header="false" direction="ltr">
      <h2 class="park_list_title">车位列表</h2>
      <ParkList :parkList="parkList"></ParkList>
    </el-drawer>
  </div>
</template>

<script>
import { req1 } from "@/network/request";
import axios from "axios";
import toast from "@/components/Toast";
import SearchInput from "@/components/SearchInput";
import OrderList from "@/components/OrderList";
import ParkList from "@/components/ParkList";

export default {
  name: "detail",
  components: {
    toast,
    SearchInput,
    OrderList,
    ParkList
  },
  data() {
    return {
      chargeMsg: [],
      show: true,
      drawer: false,
      drawer1: false,
      placeholder: "请输入订单号",
      orderList: {},
      parkList: [],
      orderCarList: [],
      i: 4
    };
  },
  updated() {
    this.getOrder();
  },
  methods: {
    showToast(i) {
      this.i = i
      try {
        console.log(i);
        if (i == 0) {
          this.show = false;
          req1({
            url: "/findAllCharge"
          }).then(res => {
            this.chargeMsg = res;
          });
        } else if (i == 3) {
          this.show = true;
          this.drawer = true;
        } else if (i == 1) {
          try {
            axios({
              url: "http://30r97104b2.qicp.vip/park/findAllPark"
            }).then(res => {
              console.log(res.data);
              this.drawer1 = true;
              this.parkList = res.data;
            });
          } catch (error) {
            console.log(error);
          }
        } else if(i == 2){
          this.show = false;
          req1({
            url: '/findAllOrder'
          }).then(res => {
            console.log(res);
            this.orderCarList = res
          })
        }
      } catch (error) {
        console.log(error);
      }
    },
    closeToast(i) {
      this.show = i;
    },
    getOrder() {
      if (this.$refs.order.orderList[0]) {
        this.orderList = this.$refs.order.orderList[0];
      } else {
        console.log("没有数据");
      }
    }
  }
};
</script>

<style>
.detail_title {
  font-size: 24px;
  margin: 10px;
}
.navigates {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
}
.navigates > div {
  padding: 30px 30px 30px 10px;
  text-align: center;
}
.navigates > div img {
  width: 90px;
}
.navigates span {
  margin-left: 20px;
}
.search_input {
  text-align: center;
  margin-top: 50px;
}
.mousemove {
  height: 30px;
  opacity: 0;
}
.hidden {
  text-align: center;
}
.park_list_title {
  display: flex;
  justify-content: center;
  margin: 35px;
  font-size: 22px;
}
</style>