<template>
  <div id="chart">
    <div id="myChart" :style="{width: '100%', height: '100%'}"></div>
  </div>
</template>

<script>
import { req1 } from "@/network/request";
export default {
  name: "Chart",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  mounted() {
    req1({
      url: "/historyOrderNumber"
    }).then(res => {
      let date = []
      let num = []
      res.forEach(v => {
        date.push(v.orderDate)
        num.push(v.orderNumber)
      });
      this.drawLine(date,num);
    });
  },
  methods: {
    drawLine(date,num) {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      // 绘制图表
      let option = {
        title: {
          text: "历史订单数"
        },
        tooltip: {
          trigger: "axis"
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: date
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "订单数",
            type: "line",
            stack: "总量",
            data: num
          }
        ]
      };

      myChart.setOption(option);
    }
  },
  props: {
    orderArr: {
      type: Array
    }
  }
};
</script>

<style>
#chart {
  height: 100%;
}
</style>