<template>
  <div id="searvh_input">
    <div class="input">
      <el-input v-model="input" :placeholder="placeholder"></el-input>
    </div>
    <el-button type="primary" icon="el-icon-search" @click="searchClick()">搜索</el-button>
  </div>
</template>

<script>
import { req1 } from "@/network/request";
export default {
  name: "searchinput",
  data() {
    return {
      input: '',
      orderList: []
    }
  },
  props: {
    placeholder: {
      type: String,
      defaule: '请输入内容'
    }
  },
  methods: {
    searchClick() {
      console.log(this.input);
      req1({
        url: '/findOrderById',
        params:{
          orderId: this.input
        }
      }).then(res => {
        this.orderList = res
        console.log(res);
        
      })
    }
  }
};
</script>

<style>
#searvh_input{
  display: inline-block;
}
.input{
  width: 300px;
  display: inline-block;
}
</style>