<template>
  <div id="home_box_main">
    <div id="home_box_main" v-show="isShow">
      <div style="display:none;">{{index1}}</div>
      <div class="page_top">
        <div class="total_price">
          <i class="iconfont icon-renminbi"></i>
          <div class="price">
            <h2>{{historyOrderMoney}}</h2>
            <br />
            <h3>历史成交总额</h3>
          </div>
        </div>
        <div class="total_order">
          <i class="iconfont icon-fenlei"></i>
          <div class="price">
            <h2>{{todayOrderNumber}}</h2>
            <br />
            <h3>今日车位订单数量</h3>
          </div>
        </div>
        <div class="total_user_num">
          <i class="iconfont icon-yonghu"></i>
          <div class="price">
            <h2>{{peopleNum}}</h2>
            <br />
            <h3>用户数</h3>
          </div>
        </div>
      </div>
      <div class="page_center">
        <div class="history_order">
          <i class="iconfont icon-dingdan" style="color:#000;"></i>
          <div>
            <h2>{{historyOrderNumber}}</h2>
            <h2>历史成交订单数</h2>
          </div>
        </div>
        <div class="order_num">
          <i class="iconfont icon-shouye" style="color:#000;"></i>
          <div>
            <h2>{{todayOrderMoney}}</h2>
            <h2>今日成交总额</h2>
          </div>
        </div>
        <div class="charge_user_num">
          <i class="iconfont icon-tingchewei" style="color:#000;"></i>
          <div>
            <h2>7</h2>
            <h2>车位数量</h2>
          </div>
        </div>
      </div>
      <div class="page_bottom">
        <div class="order_chart">
          <chart></chart>
        </div>
        <div class="user_num_chart">
          <detail></detail>
        </div>
      </div>
    </div>
    <div class="home_box_user_main" v-show="!isShow">
      <div class="change_page">
        <el-pagination
          background
          layout="prev, pager, next"
          @current-change="current_change"
          :total="total"
          :page-size="pagesize"
        ></el-pagination>
      </div>
      <br />
      <br />
      <el-table :data="tableData" style="width: 100%;margin: 0 auto;">
        <el-table-column label="id" width="300">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <p>用户名: {{ scope.row.username }}</p>
              <p>余额: {{ scope.row.money }}</p>
              <div slot="reference" class="name-wrapper">
                <el-tag size="medium">{{ scope.row.id }}</el-tag>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="用户名" width="300">
          <template slot-scope="scope">
            <i class="el-icon-user"></i>
            <span style="margin-left: 10px">{{ scope.row.username }}</span>
          </template>
        </el-table-column>
        <el-table-column label="余额" width="300">
          <template slot-scope="scope">
            <i class="el-icon-suitcase"></i>
            <span style="margin-left: 10px">{{ scope.row.money }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <!-- <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button> -->
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import chart from "@/components/Chart";
import detail from "@/components/Detail";

import { req1 } from "@/network/request";

export default {
  inject:['reload'],
  data() {
    return {
      isShow: true,
      tableData: [],
      allArr: [],
      total: 0,
      pagesize: 8,
      currentPage: 0,
      placeholder: "请输入用户名",
      historyOrderNumber: 0,
      historyOrderMoney: 0,
      todayOrderNumber: 0,
      todayOrderMoney: 0
    };
  },
  methods: {
    handleEdit(index, row) {
      console.log(index, row);
      this.peopleNum
    },
    handleDelete(index, row) {
      console.log(index, row);
      req1({
        url: "/deleteUser",
        params: {
          id: row.id
        }
      }).then(function(res) {
        console.log(res);
        if (res == "success") {
          // this.reload()
          window.location.reload() 
        }
      });
    },

    current_change(e) {
      this.currentPage = e - 1;

      let arr = this.allArr;
      if (arr.length > this.pagesize) {
        this.tableData = arr.slice(
          this.currentPage * this.pagesize,
          (this.currentPage + 1) * this.pagesize
        );
      }
      console.log(this.tableData);
    }
  },
  props: {
    index1: {
      type: Number,
      default: 1
    },
    peopleNum: {
      type: Number,
      default: 0
    }
  },
  created() {
    // 所有用户数
    req1({
      url: "/findAllUser"
    }).then(res => {
      this.total = res.length;
      this.allArr = res;
      this.tableData = res.slice(
        this.currentPage * this.pagesize,
        (this.currentPage + 1) * this.pagesize
      );
    });
    // 历史成交订单数
    req1({
      url: "/historyOrderNumber"
    }).then(res => {
      let num = 0;
      res.forEach(v => {
        num += v.orderNumber;
      });
      this.historyOrderNumber = num;
    });
    // 历史成交金额
    req1({
      url: "/historyOrderMoney"
    }).then(res => {
      this.historyOrderMoney = res;
    });
    // 今日车位订单数
    req1({
      url: "/todayOrderNumber"
    }).then(res => {
      this.todayOrderNumber = res;
    });
    // 今日成交总额
    req1({
      url: "/todayOrderMoney"
    }).then(res => {
      this.todayOrderMoney = res;
    });
  },
  updated: function() {
    this.index1 == 1 ? (this.isShow = true) : (this.isShow = false);
  },
  components: {
    chart,
    detail
  }
};
</script>

<style>
.page_top,
.page_center {
  display: flex;
  width: 100%;
  height: 90px;
  margin-bottom: 30px;
}
.page_top {
  padding: 0;
}
.page_bottom {
  display: flex;
  width: 100%;
  height: 430px;
}
.page_center {
  height: 70px;
}
.total_price {
  flex: 1;
  background-color: rgb(19, 197, 165);
  margin: 0 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.total_order {
  flex: 1;
  background-color: rgb(251, 143, 111);
  margin: 0 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.total_user_num {
  flex: 1;
  background-color: rgb(170, 121, 210);
  margin: 0 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.history_order {
  flex: 1;
  margin: 0 10px;
  background-color: #fff;
  display: flex;
  align-items: center;
}
.order_num {
  flex: 1;
  margin: 0 10px;
  background-color: #fff;
  display: flex;
  align-items: center;
}
.charge_user_num {
  flex: 1;
  margin: 0 10px;
  background-color: #fff;
  display: flex;
  align-items: center;
}
.order_chart {
  background-color: #fff;
  flex: 2;
  margin: 0 10px;
}
.user_num_chart {
  background-color: #fff;
  flex: 1;
  margin: 0 10px;
}

.total_price .iconfont,
.total_order .iconfont,
.total_user_num .iconfont {
  flex: 1;
  text-align: right;
  border-right: 2px #fff solid;
  padding-right: 20px;
  font-size: 40px;
}

.total_price div,
.total_order div,
.total_user_num div {
  flex: 3;
  text-align: left;
  margin-left: 10px;
  color: #fff;
}
.total_price div h2,
.total_order div h2,
.total_user_num div h2 {
  font-size: 25px;
}

.history_order .iconfont,
.order_num .iconfont,
.charge_user_num .iconfont {
  flex: 1;
  font-size: 35px;
  text-align: right;
  border-right: 2px #000 solid;
  padding-right: 20px;
}

.history_order div,
.order_num div,
.charge_user_num div {
  flex: 3;
  font-size: 18px;
  margin-left: 10px;
}
.change_page {
  display: inline-block;
  position: absolute;
  left: 310px;
  margin-bottom: 20px;
}
</style>